.about__items {
  display: grid;
  grid-template-columns: 440px 600px;
  grid-template-rows: auto auto auto;
  gap: 40px 50px;
  margin-top: 60px;
  padding: 0 70px;
}

.about__item {
  padding: 25px 30px;
  background-color: $skills-back;
  border-radius: 10px;
  box-shadow: 3px 3px 5px $skills-shadow, -2px -2px 5px $skills-shadow;

  &--profile {
    grid-row-start: 1;
    grid-row-end: 3;
  }

  &--work {
    grid-row-start: 1;
  }

  &--education {
    grid-row-start: 2;
  }

  &--courses {
    grid-row-start: 3;
    grid-column-start: 1;
    grid-column-end: 3;
  }
}

.about__profile {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
}

.about__photo-wrapper {
  position: relative;
  width: 300px;
  margin-left: 20px;
  margin-bottom: 30px;
  box-shadow: -2px 0 30px $menu-button-active;

  &::before {
    content: "";
    position: absolute;
    top: 7px;
    left: 40px;
    width: 300px;
    height: 400px;
    background-color: $title;
    transform: rotate(3deg);
    opacity: 0.6;
  }
}

.about__photo {
  display: block;
  opacity: 0.9;
}

.about__text {
  @include indents-none;
  margin-bottom: 10px;

  &--dates {
    font-size: 16px;
    font-weight: 700;
    color: $flick-bright;
  }

  &--small {
    font-size: 16px;
  }
}

.about__title {
  font-weight: 700;
  font-size: 22px;
  text-transform: uppercase;
  color: $button-border;
  text-shadow: 2px 2px 5px $button-border-active;
}

.about__interests {
  font-size: 28px;
}

.about__interests i {
  position: relative;
  margin-right: 15px;

  &:last-child {
    margin-right: 0;
  }
}

.about__tooltip {
  position: absolute;
  top: 100%;
  left: 50%;
  opacity: 0;
  padding: 10px;
  font-family: $main-font;
  font-weight: normal;
  font-size: 18px;
  color: $main-text;
  text-align: center;
  white-space: nowrap;
  background-color: $button-active;
  border-radius: 5px;
  z-index: -1;
  transform: translateX(-50%);

  &::before {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -8px;
    border: 8px solid $button-active;
    border-top-color: transparent;
    border-right-color: transparent;
    border-left-color: transparent;
  }
}

.about__interests i:hover .about__tooltip,
.about__interests i:focus .about__tooltip {
  opacity: 1;
  z-index: 10;
  top: 150%;
  transition: 0.5s;
}

.about__section-title {
  @include indents-none;
  font-size: 28px;
  text-transform: uppercase;
  color: $menu-button-text-shadow;
  text-shadow: 2px 4px 5px $menu-button-shadow;
}

.about__section-title i {
  color: $main-text;
  text-shadow: none;
}

.about__text-section {
  margin-top: 25px;
}

.about__slider {
  --currentSlide: 0;
  box-sizing: border-box;
  position: relative;
  width: 100%;
  margin-top: 30px;
  padding: 0 95px;
}

.about__slider-btn {
  @include indents-none;
  @include button-reset;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 50px;
  height: 50px;
  font-size: 30px;
  color: $main-text;
  cursor: pointer;
  transition: font-size 0.5s;
  z-index: 1;

  &--previous {
    left: 0;
  }

  &--next {
    right: 0;
  }

  &:hover {
    font-size: 40px;
  }
}

.about__slider-content {
  overflow: hidden;
}

.about__slides {
  box-sizing: border-box;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  padding: 25px;
  transform: translateX(calc(-275px * var(--currentSlide)));
  transition: transform 0.8s;
}

.about__slide {
  margin-right: 25px;
}

.about__slide img {
  display: block;
  opacity: 0.8;
  box-shadow: 1px 0 10px $menu-button-active;
  transition: opacity 0.5s, transform 0.5s;
}

.about__image {
  max-width: 200px;
  margin-right: 15px;
  margin-bottom: 10px;
  text-align: center;

  &:nth-child(5) {
    margin-right: 0;
  }
}

.about__slide-link {
  display: block;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 250px;
    height: 351px;
    background-color: #63d471;
    background-image: linear-gradient(315deg, #63d471 0%, #233329 74%);
    opacity: 0.3;
    transition: opacity 0.5s, transform 0.5s;
    z-index: 2;
  }
}

.about__slide-link:hover {
  &::before {
    transform: scale(1.1);
    opacity: 0;
  }
}

.about__slide-link:hover img {
  transform: scale(1.1);
  opacity: 1;
}
